    display: block;
    width: 1px;
    height: 1em;
    content: "|";
    color: #000;
    position: relative;
}

ul.org-chart.tier-1-block li a:after,
ul.org-chart li ul.tier-2-block li a:after,
ul.org-chart li ul.tier-2-block li ul.tier-3-block li a:after,
ul.org-chart li ul.tier-2-block li ul.tier-3-block li ul.tier-4-block li a:after {
    left: 50%;
    bottom: 0;
}

ul.org-chart li ul.tier-2-block {
    display: inline-flex;
    flex-grow: 1;
    width: 100%;
}

ul.org-chart.reporting-units-block ul.tier-2-block li.tier-2 a:after,
ul.org-chart.reporting-units-block ul.tier-2-block li.tier-2 ul.tier-3-block li.tier-3 a:after {
    content: " ";
}

ul.org-chart.reporting-units-block ul.tier-2-block li.tier-2.provost a:after {
    content: "|";
}

ul.org-chart.reporting-units-block li ul.tier-2-block li.tier-2 a.first:before {
    display: block;
    width: 1px;
    height: 1em;
    content: "|";
    color: #000;
    position: relative;
    margin-left: 50%;
    margin-top: -.8em;
    margin-bottom: .8em;
}

/* Specifics */
ul.org-chart.reporting-units-block li ul.tier-2-block li.tier-2.chancellors a.first:before,
ul.org-chart.reporting-units-block li ul.tier-2-block li.tier-2.director a.first:before {
    top: -.8em;
    height: 1.2em;
    margin-bottom: 0;
}

.section-wrapper.dept-college.angle-bg {
    padding: inherit;
}

.dept-college .college-dept-wrap {
    background-color: #fff;
    padding: .8em;
    margin-top: .5em;
}

.section-wrapper.dept-college ul {
    padding-left: 1em;
}

.section-wrapper.dept-college ul li {
    list-style: none;
    font-size: .9rem;
    border-top: 1px solid #efefef;
    padding: .5em 0;
}

.section-wrapper.dept-college em {
    font-size: 1rem;
}

.section-wrapper.dept-college em:first-of-type {
    display: block;
    padding-top: 2em;
}

@media screen and (max-width: 691px) {
    ul.org-chart.reporting-units-block li.provost {
        order: 1;
    }

    ul.org-chart.reporting-units-block li.chancellors {
        order: 2;
    }

    ul.org-chart.reporting-units-block li.director {
        order: 3;
    }

    ul.org-chart li ul.tier-2-block {
        display: flex;
        flex-direction: column;
    }

    ul.org-chart li {
        list-style: disc;
        width: 100%;
        padding-left: 0;
    }

    ul.org-chart li.tier-1 a {
        background: transparent;
        border: 0;
        text-align: left;
    }

    ul.org-chart li a:after,
    ul.org-chart.reporting-units-block ul.tier-2-block li.tier-2.provost a:after,
    ul.org-chart.reporting-units-block li ul.tier-2-block li.tier-2 a.first:before {
        display: none;
    }

    li.tier-2.provost {
        margin-left: 2em;
    }

    li.tier-2.provost li {
        margin-left: 2em;
    }

    ul.org-chart li.chancellors,
    ul.org-chart li.director {
        margin-left: 2em;
    }
}
